.homePage{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-content: stretch;
    margin: 0 15%;
    height: 100vh;
    width: 90%;
    /* font-family: 'Concert One', cursive; */
    /* font-family: 'Lobster Two', cursive; */
    font-family: 'Muli', sans-serif;
    font-size: 1em;

}
.homePage>h1{
    padding: 1em;
    text-align: center;
}
.homePage>h1>span{
    font-size: 2em;
    color: rgb(217, 245, 242);
    text-align: center;
}
.homePage>p{
    display: inline;
    font-size: 1.2em;
    text-align: center;
}
.icons{
    display: flex;
    flex-direction: row;
    justify-content: center;
    size: 3em;
}
.icons>a{
    padding: 0.5em;
    border: 1.2px solid whitesmoke;
    border-radius: 50%;
    margin:0.1em 1em;
    color: whitesmoke;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}
.icons>a:hover{
    color: #282c34;
    border: 1.2px solid #282c34;
    transition: 0.6s;
}
/* .icons>svg{
    size: 10px;
} */
.myName{
	-webkit-animation:myName 0.8s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation:myName 0.8s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

 @-webkit-keyframes myName {
    0% {
      letter-spacing: -0.5em;
      transform: translateZ(-700px) translateY(-500px);
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      transform: translateZ(0) translateY(0);
      opacity: 1;
    }
  }
  @keyframes myName {
    0% {
      letter-spacing: -0.5em;
      transform: translateZ(-700px) translateY(-500px);
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      transform: translateZ(0) translateY(0);
      opacity: 1;
    }
  }
  
.aboutPage{
    display: flex;
    /* justify-content: center; */
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    height: 100%;
    width: 90%;
}
.aboutPage>h1{
    display: flex;
    font-size: 3rem;
    justify-content: center;
    margin-top: 1em;

}
.aboutPage-content{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 100%;
}
.aboutPage-img-div{
    display: flex;
    align-content: flex-start;
}
.aboutPage-image{
    max-width: 28rem;
    max-height: 100%;
    transition: transform 450ms;
    box-shadow: 1px 1px 12px rgba(130, 259, 242, 0.7);
}
.aboutPage-image:hover{
    transform: scale(1.08);
}
.aboutPage-content-details{
    display: flex;
    flex-direction: column;
    align-content: center;
    /* margin-right: 5em; */
    padding: 0.1em 1em;
    max-width: 25em;
}
.aboutPage-content-details>p{
    text-align: justify;
}
.aboutPage-content-details ul{
    padding: 0;
}
.aboutPage-content-details ul li{
    text-decoration: none;
    list-style: none;
}
.aboutPage-content-details ul li b{
    min-width: 120px;
    display: inline-block;
    position: relative;
    margin-right: 7px;
}
.aboutPage-content-details ul li b::after{
    content: ' : ';
    position: absolute;
    top: 0;
    left: auto;
    right: 0;
}
.btn-pdf{
	box-shadow:inset 0px 1px 0px 0px #2a8386;
	background:linear-gradient(to bottom, #282c34 5%, #464e57 100%);
	background-color:#282c34;
	border-radius:6px;
	border:1px solid #84bbf3;
	display:inline-block;
	cursor:pointer;
	color:whitesmoke;
	font-family:Courier New;
	font-size:18px;
	font-weight:bold;
	padding:8px 10px;
	text-decoration:none;
    text-shadow:0px 1px 0px #528ecc;
    text-align: center;
}
.btn-pdf:hover {
	background:linear-gradient(to bottom, #282c34 5%, #2a8386 100%);
	background-color:#464e57;
}
.btn-pdf:active {
	position:relative;
	top:1px;
}
.services{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 100%;
    text-align: center;
    margin:1em 0;
}
.services-div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color:#282c34 ;
    margin: 2em 0;
    padding: 1em 2em;
    align-content: center;
    width: 15em;
}
.services-div img{
    align-self: center;
    width: 10em;
    vertical-align: middle;
    height: 10em;
    border-radius: 70%;
    border: 4px solid #2a8386;
    /* cursor:pointer ; */
    transition: transform 0.3s;
}
.services-div img:hover{
    transform: scale(1.05);
}
.services-div>p{
    text-align: justify;
}
.services-div>h5{
    font-size: 1.3em;
    margin-bottom: 0.1em;
}
.aboutPage-content-details>h3,
.aboutPage-content-details>p{
    margin-bottom: 0.1em;
}
@media(max-width : 360px){
    .aboutPage-image{
        max-width: 18rem;

    }
}
.contactPage{
