}
.contactPage h1{
    font-size: 2em;
    margin: 0 auto;
}
.contact-info{
    display: flex;
    flex-direction: column;
    align-content: space-around;
    flex-wrap: wrap;
    margin: 0 auto;

}
.contact-info-block{
    display: flex;
    flex-direction: row;
    align-content: center;
    flex-wrap: wrap;
    padding: 0.2em 1em;
    background-color: #282c34;
    margin-top: 1em;
}
.contact-icon{
    color: whitesmoke;
    border: 1px solid whitesmoke;
    padding: 1em;
    align-self: center;
    margin: auto 0;

}
.contact-content{
    display: flex;
    flex-direction: column;
    padding: 0.2em 0.8em;

}
.contact-content h4{
    font-size: 1.4em;
    margin-bottom: 0;

}
.contact-content p{
    font-size: 1.1em;
}
.contact-info-block{
	-webkit-animation: contact-info-block 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: contact-info-block 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

 @-webkit-keyframes contact-info-block{
    0% {
      transform: translateZ(600px);
      opacity: 0;
    }
    100% {
      transform: translateZ(0);
      opacity: 1;
    }
  }
  @keyframes contact-info-block {
    0% {
      transform: translateZ(600px);
      opacity: 0;
    }
    100% {
      transform: translateZ(0);
      opacity: 1;
    }
  }
  
